import React from 'react';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faGithub } from '@fortawesome/free-brands-svg-icons';

const Footer = ({ mode }) => {
  const footerStyle = {
    display: 'flex',
    justifyContent: 'center',
    alignItems: 'center',
    position: 'fixed',
    bottom: '0',
    width: '100%',
    backgroundColor: mode === 'dark' ? '#333' : '#f1f1f1',
    color: mode === 'dark' ? '#fff' : '#000',
    textAlign: 'center',
    padding: '4px',
    zIndex: '1000',
    fontSize: '15px',
  };

  const linkStyle = {
    marginLeft: '5px',
    color: mode === 'dark' ? '#fff' : 'black',
    textDecoration: 'none',
  };

  const iconStyle = {
    marginLeft: '5px',
    fontSize: '18px',
    color: mode === 'dark' ? '#fff' : '#000',
  };

  const heartStyle = {
    margin: '0 3px', // Adjust the margin to add space on both sides of the heart
  };

  return (
    <footer style={footerStyle}>
      Made with <span role="img" aria-label="heart" style={heartStyle}>❤️</span> by Sayan-Roy
      <a href='https://github.com/RoyXSayan' style={linkStyle} target='_blank' rel="noopener noreferrer">
        <FontAwesomeIcon icon={faGithub} style={iconStyle} />
      </a>
    </footer>
  );
};

export default Footer;
